export default class Stopwtach {
    interval: any
    currentSecond = 0
    currentHundredth = 0
    limit: number = 0
    constructor(
        public onUpdateSecond: (second: number) => void | null,
        public onUpdateHundredth: (hundredth: number) => void | null,
        public finishCallback: () => void | null) { }

    public start() {
        this.interval = setInterval(() => {

            this.currentHundredth += 1
            if (this.currentHundredth >= 100) {
                this.currentSecond += 1
                this.currentHundredth = 0

                this.onUpdateSecond(this.currentSecond)
            }

            this.onUpdateHundredth(this.currentHundredth)

            if (this.currentSecond >= this.limit) {
                this.finishCallback()
            }
        }, 10)
    }

    public stop() {
        clearInterval(this.interval)
    }

    public reset(seconds: number) {
        this.limit = seconds
        this.currentSecond = 0
        this.currentHundredth = 0
        this.onUpdateSecond(this.currentSecond)
        this.onUpdateHundredth(this.currentHundredth)
    }
}

