import {
  createStyles,
  Fab,
  makeStyles,
  Theme,
  Typography,
} from "@material-ui/core";
import { PlayArrow, Pause, Restore } from "@material-ui/icons";
import { padStart, toInteger, toString } from "lodash";
import React, { useEffect, useRef, useState } from "react";
import { useParams } from "react-router-dom";
import { useLocalStorageState } from "../../hooks/useLocalStorageState";
import { TimerConfiguration } from "../../types/timerConfiguration";
import Stopwtach from "../../util/stopwatch";
import { Title } from "./title";
import clsx from "clsx";

const audioUrl =
  "https://notificationsounds.com/storage/sounds/file-sounds-1235-swift-gesture.mp3";
const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: { display: "flex", flexDirection: "column", height: "100%" },
    play: {
      display: "flex",
      flex: 2,
      alignItems: "center",
      justifyContent: "space-evenly",
      flexWrap: "wrap",
    },
    display: {
      display: "flex",
      flex: 2,
      alignItems: "center",
      justifyContent: "center",
    },
    displayWrapper: {
      display: "flex",
      flexDirection: "row",
      padding: "62px 40px",
      border: "solid 0.5px",
      borderRadius: "150px",
      boxShadow: "0px 2px 20px 4px",
    },
  })
);
let stopwatch: Stopwtach | null = null;
const Play: React.FC<{}> = () => {
  const secRef = useRef<any>();
  const humRef = useRef<any>();
  const classes = useStyles();
  const { index } = useParams();

  const [x] = useLocalStorageState("timerList", []);
  const list = x as TimerConfiguration[];

  const timeConfig = list[toInteger(index)];

  const [isWorkingout, setIsWorkingout] = useState(true);
  const [isPlaying, setIsPlaying] = useState(false);

  useEffect(() => {
    stopwatch = new Stopwtach(
      (second) => {
        console.log({ second });
        secRef.current.innerText = padStart(toString(second), 2, "0");
      },
      (hundredth) => {
        humRef.current.innerText = padStart(toString(hundredth), 2, "0");
      },
      () => {
        navigator.vibrate([500, 200, 500]);
        var audio = new Audio(audioUrl);
        audio.play();

        setIsWorkingout((x) => !x);
      }
    );

    return () => stopwatch?.stop();
  }, [setIsWorkingout, timeConfig]);

  useEffect(() => {
    stopwatch!.reset(
      isWorkingout ? timeConfig.workOutTimer : timeConfig.restTimer
    );
  }, [isWorkingout, timeConfig]);

  useEffect(() => {
    if (isPlaying) stopwatch?.start();
    else stopwatch?.stop();
  }, [isPlaying]);

  useEffect(() => {
    setIsWorkingout(true);
  }, [timeConfig]);

  return (
    <div className={classes.root}>
      <Title isWorkingout={isWorkingout} timeConfig={timeConfig} />
      <div className={classes.display}>
        <div
          className={clsx(classes.displayWrapper, isPlaying && "pulse")}
          onClick={() => setIsPlaying(!isPlaying)}
        >
          <h3
            style={{
              fontSize: "6rem",
              fontFamily: '"Roboto", "Helvetica", "Arial", sans-serif',
              fontWeight: 400,
              lineHeight: 1.167,
              letterSpacing: "0em",
              margin: 0,
            }}
            ref={secRef}
          >
            00
          </h3>
          <p
            style={{
              fontSize: "3rem",
              fontFamily: '"Roboto", "Helvetica", "Arial", sans-serif',
              fontWeight: "400",
              lineHeight: "1.5",
              letterSpacing: "0.00938em",
              margin: 0,
            }}
            ref={humRef}
          >
            00
          </p>
        </div>
      </div>
      <div className={classes.play}>
        <Fab
          color="primary"
          aria-label="add"
          size="large"
          onClick={() => {
            setIsPlaying(false);
            stopwatch?.reset(timeConfig.workOutTimer);
            setIsWorkingout(true);
          }}
        >
          <Restore />
        </Fab>
      </div>
    </div>
  );
};

export default Play;
